.card {
	background-color: #fff;
	border-radius: 10px;
	overflow: hidden;
	max-width: 282px;
	position: relative;
}

.card__image {
	margin-bottom: 25px;
	width: 282px;
	height: 282px;
}
.card__image:hover {
	cursor: pointer;
}
.card__title-wrapper {
	display: flex;
	justify-content: space-between;
	max-width: 246px;
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 31px;
}
.card__title {
	margin: 0;
	color: #000000;
	font-family: "Inter", Arial, sans-serif;
	font-style: normal;
	font-weight: 900;
	font-size: 24px;
	line-height: 29px;
	max-width: 220px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	align-self: center;
}
.card__like-btn-wrapper {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	row-gap: 3px;
	align-self: center;
}
.card__like-btn-counter {
	font-family: "Inter", Arial, sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 13px;
	line-height: 16px;
	text-align: center;
	color: #000000;
	margin: 0;
}
.card__like-btn {
	padding: 0;
	border: none;
	background-image: url("../images/like_icon.svg");
	background-color: transparent;
	align-self: center;
	transition: ease 0.2s;
	opacity: 1;
	width: 21px;
	height: 19px;
}
.card__like-btn:hover {
	cursor: pointer;
	opacity: 0.5;
}
.card__like-btn-filled {
	background-image: url("../images/like_icon_filled.svg");
}
.card__delete-btn {
	padding: 0;
	border: none;
	background-color: transparent;
	background-image: url("../images/delete_button.svg");
	transition: ease 0.2s;
	opacity: 1;
	width: 18px;
	height: 19px;
	position: absolute;
	top: 18px;
	right: 15px;
}
.card__delete-btn-hidden {
	visibility: hidden;
}
.card__delete-btn-visible {
	visibility: visible;
}
.card__delete-btn:hover {
	cursor: pointer;
	opacity: 0.6;
}
