.form {
	display: block;
	margin: 0;
	padding-bottom: 36.51px;
}

.form__fieldset {
	border: 0;
	padding: 0;
	margin-top: 0;
	min-width: 0;
	max-width: 76.7%;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 34px;
}
.form__fieldset_type_auth {
	max-width: 100%;
}
.form__fieldset_type_submit {
	margin-bottom: 0;
}
.form__input {
	width: 100%;
	font-family: "Inter", Arial, sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 17px;
	color: #000000;
	border: 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	box-sizing: content-box;
	padding-bottom: 13px;
}

.form__input::placeholder {
	color: #838383;
}
.form__input_type_error {
	border-bottom: 1px solid #ff0000;
}
.form__input-error {
	font-family: "Inter", Arial, sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 12px;
	line-height: 15px;
	margin-top: 5px;
	color: #ff0000;
	display: none;
}
.form__input-error_active {
	display: block;
}
.form__button {
	margin-top: calc(48px - 34px);
	width: 100%;
	height: 50.15px;
	font-family: "Inter", Arial, sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	border: 0;
	border-radius: 2px;
	box-sizing: border-box;
	background-color: #000;
	color: #fff;
	transition: ease 0.2s;
	opacity: 1;
}
.form__button:hover {
	opacity: 0.8;
	cursor: pointer;
}
.form__button_disabled {
	background-color: #fff;
	color: #000;
	border: 1px solid #000000;
	opacity: 0.2;
	user-select: none;
}
.form__button_disabled:hover {
	cursor: inherit;
	opacity: 0.2;
}

@media screen and (max-width: 597px) {
	.form {
		padding-bottom: 25px;
	}
	.form__fieldset {
		max-width: 84.4%;
	}
	.form__button {
		font-size: 14px;
		line-height: 17px;
		margin-top: calc(46px - 30px);
		height: 46px;
	}
	.form__input {
		padding-bottom: 9px;
		margin-bottom: 30px;
	}
}
